:root {
  --primary-dark: #14213d;
  --primary-accent: #ff3d00;
  --secondary-accent: #004aad;
  --text-light: #ffffff;
  --text-dark: #000000;
  --background-dark: #000000;
  --background-light: #ffffff;
}

body {
  background-color: var(--background-dark);
  color: var(--text-light);
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1rem;
}

a {
  color: var(--primary-accent);
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--secondary-accent);
}

.btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: var(--primary-accent);
  color: var(--text-light);
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.btn:hover {
  background-color: var(--secondary-accent);
  transform: translateY(-2px);
}

.section-title {
  font-size: 2.5rem;
  color: var(--primary-accent);
  text-align: center;
  margin-bottom: 2rem;
  position: relative;
}

.section-title::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background-color: var(--secondary-accent);
  margin: 1rem auto 0;
}

/* Aggiungi stili per effetti di hover energici */
.hover-effect {
  transition: all 0.3s ease;
}

.hover-effect:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(255, 61, 0, 0.3);
}

/* Aggiungi uno stile per bordi angolari e accattivanti */
.angular-border {
  position: relative;
  overflow: hidden;
}

.angular-border::before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  border: 2px solid var(--primary-accent);
  transform: skew(-2deg);
  z-index: -1;
}


@media (max-width: 768px) {
  .section-title {
    font-size: 2rem;
  }
}

.flame-text {
  background: linear-gradient(45deg, var(--primary-accent), var(--secondary-accent));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
}