:root {
  --brand-color: #ff4500;
  --brand-color-dark: #cc3700;
  --brand-color-light: #ff6a33;
  --primary-dark: #1a1a1a;
  --secondary-accent: #ffd700;
  --text-light: #ffffff;
}

.testimonials {
  position: relative;
  padding-top: 80px; /* Spazio per l'h2 */
}

.testimonials h2 {
  position: relative;
  z-index: 3;
  color: var(--brand-color);
  font-family: 'Oswald', sans-serif;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 3rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 
    2px 2px 0 var(--brand-color-dark),
    6px 6px 0 rgba(0,0,0,0.2);
  opacity: 1;
  display: block !important;
  visibility: visible !important;
}

.testimonial-card-container {
  perspective: 1000px;
  width: 100%;
  height: 400px;
  margin: 0 auto;
}


.testimonials::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, var(--secondary-accent) 0%, transparent 70%);
  opacity: 0.05;
  animation: rotate 40s linear infinite;
  z-index: 1;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.testimonials h2:hover {
  transform: scale(1.05) translateY(-5px);
  text-shadow: 
    4px 4px 0 var(--brand-color-dark),
    8px 8px 0 rgba(0,0,0,0.2);
}

.mySwiper {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 50px;
  position: relative;
  z-index: 2;
  margin-top: 10px; /* Aggiunge spazio sopra lo Swiper */
}


.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: auto;
}

.testimonial-card {
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}

.testimonial-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.testimonial-card.flipped .testimonial-card-inner {
  transform: rotateY(180deg);
}

.testimonial-card-front, .testimonial-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 15px;
  padding: 20px;
  background-color: rgba(20, 33, 61, 0.9);
  box-shadow: 0 10px 20px rgba(255, 61, 0, 0.3);
}

.testimonial-card-back {
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.before-after-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80%;
}

.image-container {
  width: 48%;
  text-align: center;
}

.transformation-image {
  width: 100%;
  height: auto;
  max-height: 200px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
}

.image-container span {
  font-size: 0.8rem;
  color: var(--text-light);
}

.click-to-flip {
  text-align: center;
  font-size: 0.9rem;
  color: #ff6a33;
  margin-top: 10px;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}

.testimonial-text {
  color: var(--text-light);
  font-style: italic;
  margin-bottom: 20px;
  font-family: 'Roboto Condensed', sans-serif;
  line-height: 1.6;
  text-align: center;
  font-size: 1.1rem;
}

.testimonial-name {
  color: var(--secondary-accent);
  font-family: 'Oswald', sans-serif;
  font-weight: 600;
  font-size: 1.3rem;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
}

.swiper-pagination-bullet {
  background-color: var(--text-light);
  opacity: 0.7;
}

.swiper-pagination-bullet-active {
  background-color: var(--brand-color);
  opacity: 1;
}

.swiper-button-next, .swiper-button-prev {
  color: var(--brand-color);
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.swiper-button-prev:hover, .swiper-button-next:hover {
  color: var(--secondary-accent);
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .testimonials {
    padding: 40px 0;
  }

  .testimonials h2 {
    font-size: 2.2rem;
    margin-bottom: 2rem;
  }

  .swiper-slide {
    width: 280px;
  }

  .testimonial-card {
    height: 350px;
  }

  .testimonial-card-front, .testimonial-card-back {
    padding: 15px;
  }

  .transformation-image {
    max-height: 150px;
  }

  .image-container span {
    font-size: 0.7rem;
  }

  .click-to-flip {
    font-size: 0.8rem;
  }

  .testimonial-text {
    font-size: 0.9rem;
  }

  .testimonial-name {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .testimonials h2 {
    font-size: 1.8rem;
  }

  .swiper-slide {
    width: 240px;
  }

  .testimonial-card {
    height: 300px;
  }

  .transformation-image {
    max-height: 120px;
  }

  .testimonial-text {
    font-size: 0.8rem;
  }

  .testimonial-name {
    font-size: 1rem;
  }
}