:root {
  --brand-color: #ff4500; /* Sostituisci con il colore esatto del tuo brand */
  --brand-color-dark: #cc3700; /* Una versione più scura del colore del brand */
  --brand-color-light: #ff6a33; /* Una versione più chiara del colore del brand */
}

.services {
  padding: 80px 0;
  background-color: var(--primary-dark);
  position: relative;
  overflow: hidden;
}

.services::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, var(--secondary-accent) 0%, transparent 70%);
  opacity: 0.03;
  animation: rotate 60s linear infinite;
  z-index: 1;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.services-header {
  position: relative;
  z-index: 10;
  padding: 20px 0;
  background-color: transparent;
  margin-bottom: 40px;
}

.services-title {
  color: var(--brand-color);
  font-size: 3rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0;
  opacity: 0;
  transform: translateY(50px);
  text-shadow: 
    2px 2px 0 var(--brand-color-dark),
    6px 6px 0 rgba(0,0,0,0.2);
  transition: all 0.3s ease;
}

.services-title:hover {
  transform: scale(1.05) translateY(-5px);
  text-shadow: 
    4px 4px 0 var(--brand-color-dark),
    8px 8px 0 rgba(0,0,0,0.2);
}

.mySwiper {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 50px;
  position: relative;
  z-index: 5;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: auto;
  min-height: 350px;
}

.service-card {
  background-color: rgba(20, 33, 61, 0.9);
  border-radius: 8px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow-y: auto;
  color: var(--text-light);
  font-size: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(255, 61, 0, 0.3);
}

.service-card h3 {
  color: var(--primary-accent);
  font-size: 1.5rem;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.service-card p {
  color: var(--text-light);
  font-size: 1rem;
  text-align: left;
  line-height: 1.6;
}

.swiper-pagination-bullet {
  background-color: var(--text-light);
  opacity: 0.7;
}

.swiper-pagination-bullet-active {
  background-color: var(--primary-accent);
  opacity: 1;
}

.swiper-button-next, .swiper-button-prev {
  color: var(--primary-accent);
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.swiper-button-prev:hover, .swiper-button-next:hover {
  color: var(--secondary-accent);
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .services-title {
    font-size: 2.5rem;
    color: var(--brand-color);
    text-shadow: 
      2px 2px 0 var(--brand-color-dark),
      4px 4px 0 rgba(0,0,0,0.2);
  }

  .services-header {
    background-color: transparent;
  }
  .swiper-slide {
    width: 280px;
  }

  .service-card {
    padding: 20px;
  }

  .service-card h3 {
    font-size: 1.3rem;
  }

  .service-card p {
    font-size: 0.9rem;
  }
}